@charset "UTF-8";
/*----------------------------------------------------
index.html work contents
------------------------------------------------------*/
#work .work-wrapper ul {
  padding: 50px 30px;
  display: flex;
  flex-wrap: wrap;
}

#work .work-item {
  box-sizing: border-box;
  padding: 5px;
  width: 33.3%;
  min-width: 333px;
  text-align: center;
}

#work .work-item:hover .work-item li {
  padding: 50px;
}

#work figure {
  margin: 0;
  position: relative;
}

#work figcaption {
  position: absolute;
  top: -4%;
  left: -4%;
  width: 108%;
  height: 104%;
  background-color: rgba(255, 255, 255, 0);
  transition: all 0.8s;
  color: rgba(255, 255, 255, 0);
  text-align: center;
}

#work figcaption:hover {
  top: 7%;
  left: 7%;
  width: 86%;
  height: 86%;
  background-color: rgba(255, 46, 99, 0.6);
  color: rgb(255, 255, 255);
  transition: all 0.8s;
}

#work figcaption h3 {
  font-size: 1.2rem;
}

#work figcaption p {
  font-size: 0.8rem;
}

#work figcaption .fig-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

/*----------------------------------------------------
about
------------------------------------------------------*/
#about {
  padding-bottom: 70px;
}

#about .icon-wrapper {
  width: 80%;
  margin-right: 10%;
  margin-left: 10%;
  /*text-align:center;*/
  display: flex;
  justify-content: center;
}

#about .icon-wrapper img {
  width: 35vw;
  box-shadow: 0px 0px 6px #eee;
}

#about .icon-wrapper .name {
  text-orientation: mixed;
  letter-spacing: 5px;
}

#about .icon-wrapper .name .name-main {
  font-size: 3rem;
  margin-right: 10px;
  margin-top: 0;
  margin-bottom: 0;
}

#about .icon-wrapper .name .name-sub {
  margin: 0;
  color: #ff2e63;
}

#about .about-wrapper {
  width: 70%;
  padding-right: 15%;
  padding-left: 15%;
  line-height: 2.5;
}

#about .about-wrapper p {
  font-size: 1rem;
}

#about .about-wrapper h4 {
  color: #ff2e63;
  filter: drop-shadow(1px 1px 5px #c0c0c0);
  font-size: 1.4rem;
}

/*----------------------------------------------------
skill
------------------------------------------------------*/
#skill .sec-title {
  color: rgba(255, 255, 255, 0.1882352941);
}

#skill .skill-wrap {
  display: flex;
  text-align: center;
  flex-wrap: wrap;
  max-width: 80%;
  justify-content: space-around;
  margin-left: 10%;
  padding: 30px 20px;
  font-family: "Quicksand", sans-serif;
  color: #333;
}

#skill .skill-wrap .skill-item {
  text-align: center;
}

#skill .fab {
  font-size: 5rem;
  transition: all 0.5s;
}

#skill .fab:hover {
  transform: rotate(-25deg);
  transition: all 0.5s;
}

/*----------------------------------------------------
service
------------------------------------------------------*/
#service {
  width: 100%;
  background-size: cover;
  background-position: center top;
  position: relative;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

#service .sec-title {
  color: rgba(255, 255, 255, 0.1882352941);
}

#service .service-wrapper {
  color: #333;
  margin-right: 10%;
  margin-left: 10%;
  display: flex;
  box-sizing: border-box;
  text-align: center;
}

#service i {
  font-size: 3rem;
}

#service .service-wrap-right {
  width: 45%;
  padding: 30px;
  margin-bottom: 130px;
}

#service .service-wrap-right h3 {
  font-size: 1.2rem;
}

#service .service-wrap-right p {
  text-align: left;
  font-size: 1rem;
}

#service .service-wrap-left {
  width: 45%;
  margin-right: 10%;
  padding: 30px;
  margin-bottom: 130px;
}

#service .service-wrap-left h3 {
  font-size: 1.2rem;
}

#service .service-wrap-left p {
  text-align: left;
  font-size: 1rem;
}

